import { FC } from 'react';

import { TwoColumnLayoutProps } from '../Layout.types';

import { TwoColumnLayout, Main, Sidebar } from './TwoColumn.styles';

const TwoColumn: FC<TwoColumnLayoutProps> = ({
  main,
  aside,
  asideVisibleAtWidth,
  ...rest
}) => {
  if (!main) return null;

  return (
    <TwoColumnLayout {...rest}>
      <Main>{main}</Main>
      {aside && (
        <Sidebar $visibleAtWidth={asideVisibleAtWidth}>{aside}</Sidebar>
      )}
    </TwoColumnLayout>
  );
};

export default TwoColumn;
